"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useRouter } from "next/navigation"

interface Appointment {
  id: number
  date: Date
  time: string
  description: string
  status: "pending" | "approved" | "rejected"
}

export default function AdminPage() {
  const [appointments, setAppointments] = useState<Appointment[]>([])
  const router = useRouter()

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user") || "{}")
    if (!user || user.role !== "admin") {
      router.push("/")
    }

    // Simulated appointments data
    const mockAppointments: Appointment[] = [
      {
        id: 1,
        date: new Date(),
        time: "10:00",
        description: "Consulta contable general",
        status: "pending"
      },
      {
        id: 2,
        date: new Date(Date.now() + 86400000),
        time: "14:30",
        description: "Revisión de estados financieros",
        status: "pending"
      }
    ]
    setAppointments(mockAppointments)
  }, [router])

  const handleStatusChange = (id: number, status: "approved" | "rejected") => {
    setAppointments(appointments.map(appointment =>
      appointment.id === id ? { ...appointment, status } : appointment
    ))
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Panel de Administración</h2>
      </div>

      <Card className="p-6">
        <h3 className="text-xl font-semibold mb-4">Gestión de Citas</h3>
        <div className="space-y-4">
          {appointments.map((appointment) => (
            <div
              key={appointment.id}
              className="p-4 border rounded-lg space-y-3"
            >
              <div className="flex justify-between items-start">
                <div>
                  <div className="font-medium">
                    {appointment.date.toLocaleDateString()}
                  </div>
                  <div className="text-sm text-gray-600">
                    Hora: {appointment.time}
                  </div>
                  <div className="text-sm text-gray-600">
                    {appointment.description}
                  </div>
                </div>
                <div className="space-x-2">
                  {appointment.status === "pending" && (
                    <>
                      <Button
                        onClick={() => handleStatusChange(appointment.id, "approved")}
                        className="bg-green-600 hover:bg-green-700"
                      >
                        Aprobar
                      </Button>
                      <Button
                        onClick={() => handleStatusChange(appointment.id, "rejected")}
                        className="bg-red-600 hover:bg-red-700"
                      >
                        Rechazar
                      </Button>
                    </>
                  )}
                  {appointment.status === "approved" && (
                    <span className="px-3 py-1 bg-green-100 text-green-800 rounded-full text-sm">
                      Aprobada
                    </span>
                  )}
                  {appointment.status === "rejected" && (
                    <span className="px-3 py-1 bg-red-100 text-red-800 rounded-full text-sm">
                      Rechazada
                    </span>
                  )}
                </div>
              </div>
            </div>
          ))}
          {appointments.length === 0 && (
            <p className="text-gray-500 text-center">
              No hay citas pendientes
            </p>
          )}
        </div>
      </Card>

      <Card className="p-6">
        <h3 className="text-xl font-semibold mb-4">Resumen</h3>
        <div className="grid grid-cols-3 gap-4">
          <div className="p-4 bg-yellow-50 rounded-lg">
            <h4 className="text-lg font-medium text-yellow-700">Pendientes</h4>
            <p className="text-2xl font-bold text-yellow-800">
              {appointments.filter(a => a.status === "pending").length}
            </p>
          </div>
          <div className="p-4 bg-green-50 rounded-lg">
            <h4 className="text-lg font-medium text-green-700">Aprobadas</h4>
            <p className="text-2xl font-bold text-green-800">
              {appointments.filter(a => a.status === "approved").length}
            </p>
          </div>
          <div className="p-4 bg-red-50 rounded-lg">
            <h4 className="text-lg font-medium text-red-700">Rechazadas</h4>
            <p className="text-2xl font-bold text-red-800">
              {appointments.filter(a => a.status === "rejected").length}
            </p>
          </div>
        </div>
      </Card>
    </div>
  )
}
